<template>
    <div class="total-wrp">

        <div class="total">
            
            <div class="head">
                
            </div>

            <div class="story-title">
                {{ story_detail[0].title }}
            </div>

            <div class="story-begin">
                {{ begin_content }}
            </div>

            <div class="story-write">
                <div class="story-write-ins">
                    
                </div>

                <textarea class="story-write-area" v-model="content">
                </textarea>

                <div class="story-write-option">
                    <div class="submit finish" @click="finish()"></div>
                    <div class="submit give-up"></div>
                </div>
                
            </div>

        </div>
            
    </div>
</template>




<script>

import api from '../api/index'
import {backEndUrl} from '../api/index'



export default {

    beforeMount() {
        if(!this.store_util.get_session("user")) {
            this.$router.replace("/")
            return
        }
        api.storyDetail(this.id).then(
            (res) => {
                console.log(res)
                if(res.code == 0) {
                    console.log(res.data)
                    this.story_detail = res.data
                    
                    /**   */
                    let delete_list = this.store_util.get_local("delete_list")
                    delete_list[this.id] = false
                } else if(res.code == 601) {
                    window.alert(" ")
                    this.$router.replace("/")
                }
            }
        )

         window.addEventListener("beforeunload", e => this.before_unload_Fn(e))
    },
    
    data() {
        return {
           
           content: "",
           story_detail: "",
           /**  */
           begin_content: this.story_content[this.store_util.get_session("curStory").content],
           has_submit: false,
           type: this.store_util.get_session("curStory").type,
           id: this.store_util.get_session("curStory").id,
        }
    },

    methods: {

        tab(page) {
            this.curPage = page
        },

        finish() {
            if(!window.confirm("?(, )")) {
                return
            }
            this.has_submit = true
            /**  */
            api.writeStory({content: this.content, id: this.id, type: this.type}).then(
                (res)=> {
                    if(res.code == 0) {
                        /**   */
                        window.alert(` ! 
                        url:${backEndUrl}NormalWrite/${this.id}`)

                        this.$router.replace("/MainPage")
                    } else if(res.code == 601) {
                        window.alert(" ")
                        this.$router.replace("/")
                    }
                    
                }
            )
        },

        /** 
         * 
         */

        before_unload_Fn (e) {
            /**    */
        //    if(!this.has_submit) {
        //        api.deleteStory_sync(this.id)
        //    }

            if(!this.has_submit) {
                let delete_list = this.store_util.get_local("delete_list")
                /** id
                 *   idfalse
                 *  trueid
                 */

                 delete_list[this.id] = true
            }
            
        }
    }
}
</script>


<style scoped>

    /*  */
    /* rgb(9, 155, 123) */

    .total-wrp {


        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        /** tabber */
    }

    .total {
        position: relative;

        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        /** tabber */

        font-size: 20px;

        max-width: 20em;
    }

    
    .total>div {
        margin: 1em auto;
    }


    /**  */
    .story-title {

        font-size: 25px;
        font-weight: 700;

        padding: .3em .5em;

        text-align: center;
        /* margin: .5em auto; */

        border-bottom: solid 2px rgb(9, 155, 123);

        color: rgb(9, 155, 123);

        min-width: 220px;

    }

    /**  */
    .story-begin {
        padding: .5em .5em .5em .5em;
        
        min-height: 7em;
        min-width: 300px;
        text-align: left;
        font-size: 18px;

        border: solid 1px rgb(14, 51, 43);
        border-radius: 10px;

        font-weight: 100;
    }


    /**  */
    .story-write-ins {
        margin: 1em auto;
        font-weight: 700;
    }

    .story-write-area {
        min-height: 12em;
        min-width: 270px;

        max-width: 15em;

        border-radius: 1em;
        padding: 1em 1em;

        font-size: 20px;
        outline: 0;

        background: rgb(250, 246, 246);
    }

    .story-write-area:focus {
        border: 2px solid rgb(120, 182, 173);
    }


    .story-write-option {
        display: flex;
        align-items: center;
        
        margin: 1em auto;
        justify-content: space-around;
    }

    /**  */

    .story-write-option {
        max-width: 90vw;
    }

    .submit {
         display: inline-block;

         /* border: solid 2px rgb(9, 155, 123); */
         border-radius: 30px;

         color: rgb(7, 131, 121);
         background: rgb(236, 232, 232);

         text-align: center;
         padding: .5em 1em;

         margin: 1em 1em;
         

         white-space: nowrap;
    }

</style>


