<template>
    <div class="total-wrp">

        <div class="total">
            <div class="head">
                
            </div>

            <div class="ins story-block">
                <div class="ins-title" ></div>
                <div class="ins-content">1.<br />100-200<br />2.<br />tataya<br />ta<br /><br />3.<br /></div>
            </div>

            <div class="story-begin story-block">
                <div class="story-begin-title">
                    :
                    <div class="story-begin-change" @click="change_story()">
                        <img src="../assets/fresh.png" alt="" class="change-logo">
                        
                    </div>
                </div>

                <div class="story-begin-content">
                    <span> 
                        {{ content }}
                    </span>
                </div>
            </div>


            <div class="story-type story-block">
                <div class="story-type-title">
                    :
                </div>
                <div class="story-type-list">
                    <div class="story-type-list-ele" :class="{selected : type == 0}"></div>
                    <div class="story-type-list-ele" :class="{selected : type == 1}"></div>
                    <div class="story-type-list-ele" :class="{selected : type == 2}"></div>
                    <div class="story-type-list-ele" :class="{selected : type == 3}"></div>
                    <div class="story-type-list-ele" :class="{selected : type == 4}"></div>
                </div>
            </div>

            <div class="story-title story-block">
                <div class="story-title-title">
                    :
                </div>
                <div class="story-title-input-wrp">
                    <input type="text" class="story-title-input"
                    placeholder=""
                    v-model="title">
                </div>
            </div>


            <div class="submit-wrp">
                <div class="submit" @click="submit()">
                    
                </div>
            </div>
        </div>
            
    </div>
</template>




<script>
import api from '../api/index'
import store_util from '../util/store_obj'
import change_cur_story from '../api/change_story'
import {time} from '../util/date'




export default {


    /**  */
    beforeMount() {
        if(!this.store_util.get_session("user")) {
            this.$router.replace("/")
            return
        }
        api.createStory().then(
            (res) => {
                console.log(res)
                if(res.code == 0) {
                    /** type  */
                    if(res.data.type > 4) {
                        res.data.type = 2
                    }

                    this.store_util.store_session("curStory", res.data)
                    this.content = this.story_content[this.store_util.get_session("curStory").content]
                    this.type = this.store_util.get_session("curStory").type
                    this.id = this.store_util.get_session("curStory").id
                } else if(res.code == 601) {
                    window.alert(" ")
                    this.$router.replace("/")
                }    
            }
        )

         window.addEventListener("beforeunload", e => this.before_unload_Fn(e))
    },

    mounted() {
       
    },

    // beforeRouteLeave (to, from, next) {
    // // 
    // //  `this`
    //     console.log("in")

    // },
    data() {
        return {
            title: "",
            /**  */
            content: "",
            type: "",
            id: "",

            /**  */
            has_submit: false
        }
    },

    methods: {
        tab(page) {
            this.curPage = page
        },

        submit() {
            if(this.title.length < 1 || this.title.length > 35) {
        
                window.alert("")
                return
            }
            
            this.has_submit = true
            api.setTitle({id: this.id, title: this.title}).then(
                (res) => {
                    if(res.code == 0) {
                        this.$router.replace("/FirstWrite")
                    }
                }
            )
            
        },
        
        first_write() {
            api.setStoryTitle({id: this.id ,title: this.title}).then(
                (res) => {
                     this.$router.push(`/FirstWrite/${this.id}`)
                }
            )
        },

        /** type */
        change_story() {
            change_cur_story(this.id).then((res) => {
                if(res) {
                    this.content = this.story_content[this.store_util.get_session("curStory").content]
                    this.type = this.store_util.get_session("curStory").type
                    this.id = this.store_util.get_session("curStory").id
                }
            })
        },

        /** 
         * 
         */

        before_unload_Fn (e) {
            /**    */
           if(!this.has_submit) {
               api.deleteStory_sync(this.id)
               console.log("delete")
               /** 
                * 
                */


            //    let has_del = false
            //    api.deleteStory(this.id).then(
            //        (res) => {
            //            if(res.code == 0) {
            //                has_del = true
            //            }
            //        }
            //    )

             

            //    change_cur_story(this.id)
            //    .then(console.log("hhh"))
            //    let changed = typeof store_util.get_session("inChange") == Number || 0
            //    store_util.store_session("inChange", {changed: time()})
               // 
            // let now = new Date()
            // while (new Date() - now < 5000) {}
           }
        }
        
    }
}
</script>


<style scoped>

    /*  */
    /* rgb(9, 155, 123) */

    .total-wrp {


        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        /** tabber */
    }

    .total {
        position: relative;

        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        /** tabber */

        font-size: 20px;
    }

    
    .total>div {
        margin: .3em 0;
    }

    /**  */
    .head {
        position: fixed;

        background: rgba(49, 168, 168, 0.7);
        color: rgb(252, 249, 249);
        letter-spacing: .6em;

        max-width: 100vw;
        
        min-width: 350px;


        padding: 1.4em 0;
        border-radius: 0 0 10px 10px;
        margin-bottom: 1em;

        top: 0;
    }

    /**  */
    .total>div:nth-child(2) {
        margin-top: 3em;
    }

    /**  */

    .story-block>div:nth-child(1) {
        font-size: 25px;
        color: rgb(13, 104, 84);
        text-align: left;
    }

    .story-block {
        max-width: 16em;
        
    }

    /**  */
    .ins {
        text-align: left;
        color: rgb(19, 117, 96);

    }

    .ins-title {
        font-size: 20px;
    }

    .ins-content {
        font-size: 12px;
        font-weight: 800;
    }



    /**  */
    .story-begin-change {
        font-size: 12px;
        float: right;

        position: relative;
        right: -3em;
    }

    .change-logo {
        width: 2em;
        height: 2em;
        display: block;
        margin: 0 auto;
    }

    .story-begin-title {
        margin: .5em 0;
    }

    .story-begin-content {
        border: solid 1px rgb(14, 82, 67);
        border-radius: 19px;

        background: rgb(246, 244, 244);

        
        min-width: 340px;

        min-height: 140px;
        padding: .5em;

        text-align: left;

    }


    /**  */
    .story-type-list {
        display: flex;
        align-items: center;
        justify-content: start;

        min-width: 130%;
        /* max-width: 90vw; */

        border-bottom: solid 1px rgb(41, 88, 88);

    }
        /**  */
        
    .story-type-list-ele:nth-child(1) {
        color: rgb(53, 129, 196);
    }

    .story-type-list-ele:nth-child(2) {
        color: rgb(226, 133, 133);
    }
    

    .story-type-list-ele:nth-child(3) {
        color: rgb(92, 177, 165);
    }

    .story-type-list-ele:nth-child(4) {
        color: rgb(34, 120, 233);
    }

    .story-type-list-ele:nth-child(5) {
        color: rgb(209, 135, 49);
    }

        /**  */
    .story-type-list-ele {
        margin: .4em .7em;
        font-size: 18px;
        white-space: nowrap;
        letter-spacing: .2em;

        /* display: inline-block; */
    }

    .selected {

        position: relative;
        color: rgba(255,255,255,0);
    }

    .selected::before {
        content: "";
        background: rgba(0,0,0,1);
        border-radius: 2px;
        filter: blur(1.5px);

        position: absolute;
        left: -1px;
        right: -1px;
        top: -1px;
        bottom: -1px;

        z-index: -1;

        
    }


    /**  */
    .story-title-input {
        font-size: 20px;

        outline: 0;
        border: solid 1px rgb(8, 63, 51);
        border-radius: 10px;
        
        text-align: center;
        padding: .8em 2em;

        min-width: 270px;

        

        /* font-size: 25px; */

        margin: 1em 0;
    }


      /**  */

    .submit-wrp {
        text-align: center;
        font-size: 20px;
    }

    .submit {
        display: inline-block;

         /* border: solid 2px rgb(16, 99, 81); */
         border-radius: 3em;

         color: rgb(9, 104, 96);
         background: rgb(221, 221, 221);

         text-align: center;
         font-weight: 800;
         padding: .7em 3em;

         margin: 1em auto;
         margin-left: 3em;
         margin-top: 2em;
    }

    
</style>


