<template>
<div class="total-wrp">

    <div class="total" v-if="story_exist">
            <div class="head">
              
            </div>
            <div class="story-title">
                {{ story_detail[0].title }}
            </div>

            <div class="story-begin">
                {{ begin_content }}
            </div>

            <div class="story-content">
                <div class="story-content" v-for="i in story_detail.length-1" :key="i">
                <div class="story-content-name">{{ story_detail[i].writer }}</div>
                <div class="story-content-title">{{ story_detail[i].content }}</div>
            </div>
            </div>


            <div class="editable" v-if="editable">
                 <!-- "" -->
                <div class="un-ready" v-if="!ready">
                    
                    <div class="ins">
                        <div class="ins-title">:</div>

                        <!--  -->
                        <div class="ins-content"  v-if="!lastWriter">
                            1."" 
                            <br> 100~200 <br>

                            2.ta<br>
                            tata<br>
                            ta!
                            <br>
                            3.

                        </div>
                        <!--  -->
                        <div class="ins-content" v-else> 
                            1.!
                            <br>2.<br>
                            
                            <br>3.""<br>
                            
                        </div>
                    
                    </div>
                    
                    <!--  -->
                    <div class="submit" @click="ready_write()"></div>
                    
                </div>


                <!--  -->
                <div class="ready" v-else>
                <div class="story-type" v-if="!beginEdit">
                    <div class="story-type-title" v-if="!lastWriter">
                        :
                    </div>

                    <div class="story-type-list" v-if="!lastWriter">
                        <div class="story-type-list-ele" :class="{selected : type == 0}"></div>
                        <div class="story-type-list-ele" :class="{selected : type == 1}"></div>
                        <div class="story-type-list-ele" :class="{selected : type == 2}"></div>
                        <div class="story-type-list-ele" :class="{selected : type == 3}"></div>
                        <div class="story-type-list-ele" :class="{selected : type == 4}"></div>
                    </div>

                    <div class="story-type-tip">
                        ? ?
                    </div>

                    <div class="submit" @click="begin()">
                        
                    </div>

                </div>

                <div class="story-write" v-else>
                    <div class="story-write-ins">
                        
                    </div>

                    <textarea class="story-write-area" v-model="content">
                    </textarea>

                    <div class="story-write-option">
                        <div class="submit finish" @click="finish()"></div>
                        <div class="submit give-up" @click="give_up()"></div>
                    </div>
                    
                </div>

            </div> 
        </div>

        <div class="un-editable" v-else>
            
        </div>
    </div>

           

    <div v-else class="not-found">
            404
    </div>
            
</div>
</template>




<script>
import api from '../api/index'

// 20210201120121
export default {


    beforeMount() {
        if(!this.store_util.get_session("user")) {
            this.$router.replace("/")
            return
        }

        this.init()
        /**  */
        window.addEventListener("beforeunload", e => this.before_unload_Fn(e))
    },

    data() {

        return {
           
            story_exist: true,
            story_detail: [{title:""}],

            begin_content: "",

            /**  */
            id: this.$route.params.id,
            type: "",
            content: "",

            /**  */
            beginEdit: false,
            ready: false,

            /**  */
            lastWriter: false,
            /**   */
            editable: false
        }
    },

    methods: {
        /**  */
        init() {
            api.storyDetail(this.$route.params.id).then(
                (res) => {
                    if(res.code == 0) {
                        console.log(res.data)

                            /**  */
                            this.lastWriter = res.data.length == 4
                            this.editable = res.data.length < 5
                            /**  */
                            this.story_detail = res.data
                            /**  */
                            this.begin_content = this.story_content[res.data[0].content_id] 
                        
                        } else if(res.code == 601) {
                            window.alert(" ")
                            this.$router.replace("/")
                        } else {
                            this.story_exist = false
                            console.log(404)
                        }
                    }
                )
             },


        tab(page) {
            this.curPage = page
        },

        ready_write() {
            /**  */
            this.init()

            api.storyStatus(this.id).then(
                (res) => {
                    if(res.code == 0) {
                        /**  */
                        if(res.data.flag) {
                            window.alert("")
                        } else {
                            if(!this.lastWriter) {
                                this.type = Math.floor(Math.random() * 5)
                            } 
                            /**  */
                            else {
                                this.type = -1
                            }
                            this.ready = true

                            console.log(this.type)
                            /**   */
                            api.setStatus({id: this.id, status: true})
                        }
                    } else if(res.code == 601) {
                        window.alert(" ")
                        this.$router.replace("/")
                    }
                }
            )
            
        },

         finish() {
             if(window.confirm("")) {
                /**  */
                api.writeStory({content: this.content, id: this.id, type: this.type}).then(
                    (res)=> {
                        if(res.code == 0) {
                            /**   */
                            window.alert(` ! 
                            url:NormalWrite/${this.id}`)
                            this.before_unload_Fn({})

                            this.$router.replace("/MainPage")
                        } else if(res.code == 601) {
                            window.alert(" ")
                            this.$router.replace("/")
                        }
                        
                    }
                )
            }
        },

        begin() {
            this.beginEdit = true
        },

        give_up() {
            if(window.confirm("")){
                /**  */
                this.before_unload_Fn({})
            }   else {
                return
            }
        },

         /** 
         * 
         */

        before_unload_Fn (e) {
            this.ready = false
            this.beginEdit = false
            api.setStatus_sync({id: this.id, status: false}) 
        }
    }
}
</script>


<style scoped>

    /*  */
    /* rgb(9, 155, 123) */

    .total-wrp {


        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        /** tabber */
    }

    .total {
        position: relative;

        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        /** tabber */

        font-size: 20px;

        max-width: 20em;
    }

    
    .total>div {
        margin: 1em auto;
    }

    /**  */
    .head {
        position: fixed;

        background: rgba(49, 168, 168, 0.7);
        color: rgb(252, 249, 249);
        letter-spacing: .6em;

        max-width: 100vw;
        
        min-width: 350px;


        padding: 1.4em 0;
        border-radius: 0 0 10px 10px;
        margin-bottom: 1em;

        top: 0;
    }

    /**  */
    .total>div:nth-child(2) {
        margin-top: 3em;
    }


    /**  */
    .story-title {
        padding: .3em 3em;
        text-align: center;
        /* margin: .5em auto; */

        border-bottom: solid 2px rgb(9, 155, 123);

        color: rgb(9, 155, 123);

    }

    /**  */
    .story-begin {

        padding: .5em .5em .5em .5em;
        
        min-height: 6em;
        min-width: 8em;

        max-width: 90vw;

        border: solid 2px rgb(9, 155, 123);
        border-radius: 1em;

        font-weight: 100;
    }

    /**  */

     .story-content {
        font-size: 20px;
        padding: .5em 0;
     }

    .story-content-name {
        text-align: left;
        /* padding-right: 10em; */
    }

    .story-content-title {
        text-align: left;

        min-width: 16em;
        min-height: 7em;
        padding: .5em .5em .5em .5em;

        border: 2px solid rgb(5, 92, 87);
        border-radius: 1em;

        margin: .5em 0;
    }


     /**  */
    .story-type-list {
        display: flex;
        align-items: center;
        justify-content: space-around;
    }
        /**  */
        
    .story-type-list-ele:nth-child(1) {
        color: rgb(53, 129, 196);
    }

    .story-type-list-ele:nth-child(2) {
        color: rgb(226, 133, 133);
    }
    

    .story-type-list-ele:nth-child(3) {
        color: rgb(92, 177, 165);
    }

    .story-type-list-ele:nth-child(4) {
        color: rgb(34, 120, 233);
    }

    .story-type-list-ele:nth-child(5) {
        color: rgb(209, 135, 49);
    }

        /**  */
    .story-type-list-ele {
        margin: .4em .7em;
        font-size: 22px;
        white-space: nowrap;
    }
    /**  */
    .selected {
        background: black;
        color: white;
    }



    /**  */
    .story-write-ins {
        margin: 1em auto;
        font-weight: 100;
    }

    .story-write-area {
        min-height: 6em;
        min-width: 8em;

        max-width: 15em;

        border-radius: 1em;
        padding: 1em 1em;

        font-size: 20px;
        outline: 0;

    }

    .story-write-area:focus {
        border: 1.2px solid rgb(10, 230, 193);
    }


    .story-write-option {
        display: flex;
        align-items: center;
        
        margin: 1em auto;
        justify-content: space-around;
    }

    /**  */
    .submit {
         display: inline-block;

         border: solid 2px rgb(9, 155, 123);
         border-radius: 30px;

         color: rgb(9, 104, 96);

         text-align: center;
         padding: .5em 1em;

         margin: 1em 1.5em;
         margin-top: 2em;

         white-space: nowrap;
    }

</style>


